import Item1 from '../cryptocurrency-logo.jpg';
import Item2 from '../ninjatrader.jpg';
import Item3 from '../thinkorswim.jpg';
import { ADD_TO_CART,REMOVE_ITEM,SUB_QUANTITY,ADD_QUANTITY,ADD_Install } from '../actions/action-types/cart-actions';


const initState = {
    
    items: [
        {id:1,title:'Cryptocurrency', desc: "Lorem ipsum dolor ", price:110,img:Item1},
        {id:2,title:'Futures', desc: "Lorem ipsum dolor", price:80,img: Item2},
        {id:3,title:'Equities', desc: "Lorem ipsum dolor ",price:120,img: Item3}
    ],
    addedItems: [],
    total: 0,
    count: 0

}
//JSON.parse(localStorage.getItem('addedItems'))


const cartReducer= (state = initState,action)=>{
    
    //INSIDE PRODUCTS COMPONENT
    if(action.type === ADD_TO_CART){
          let addedItem = state.items.find(item=> item.id === action.id)
          //check if the action id exists in the addedItems
         let existed_item= state.addedItems.find(item=> action.id === item.id)
         if(existed_item)
         {
            addedItem.quantity += 1 

             return{
                ...state,
                addedItems: [...state.addedItems],
                 total: state.total + addedItem.price,
                 count: addedItem.quantity
                  }
        }
         else{
            addedItem.quantity = 1;
            //calculating the total
            let newTotal = state.total + addedItem.price 
            
            return{
                ...state,
                addedItems: [...state.addedItems, addedItem],
                total : newTotal,
                count: addedItem.quantity
            }
            
        }
    }
    if(action.type === REMOVE_ITEM){
        let itemToRemove= state.addedItems.find(item=> action.id === item.id)
        let new_items = state.addedItems.filter(item=> action.id !== item.id)
        
        //calculating the total
        let newTotal = state.total - (itemToRemove.price * itemToRemove.quantity )
        console.log(itemToRemove)
        return{
            ...state,
            addedItems: new_items,
            total: newTotal,
            count: new_items.quantity
        }
    }
    
    //INSIDE CART COMPONENT
    if(action.type=== ADD_QUANTITY){
        let addedItem = state.items.find(item=> item.id === action.id)
          addedItem.quantity += 1 
          let newTotal = state.total + addedItem.price
          return{
              ...state,
              addedItems: [...state.addedItems],
              total: newTotal,
              count: addedItem.quantity
          }
    }
    if(action.type=== SUB_QUANTITY){  
        let addedItem = state.items.find(item=> item.id === action.id) 
        //if the qt == 0 then it should be removed
        if(addedItem.quantity === 1){//no add to quanity
            let new_items = state.addedItems.filter(item=>item.id !== action.id)
            let newTotal = state.total - addedItem.price
            return{
                ...state,
                addedItems: [...state.addedItems],
                total: newTotal,
                count: new_items.quantity 
            }
        }
        else {
            addedItem.quantity -= 1
            let newTotal = state.total - addedItem.price
            return{
                ...state,
                total: newTotal,
                count: addedItem.quantity
            }
        }
        
    }
    if(action.type=== ADD_Install){
        return{
        ...state,
        total: state.total + 200
        }
    }
    if(action.type=== 'SUB_Install'){
        return{
        ...state,
        total: state.total - 200
        }
    }
    return state
  }
export default (cartReducer);